.wrap{
  height:42px;
  width: 100%;
  display:flex;
  justify-content: space-between;
  background: rgba(0,0,0, 0.5);
  border-radius: 15px 15px 0 0;
  box-shadow: 0px 3px 22px -2px #fff;
  border-bottom: 1px solid yellow;
  z-index:5;
}

.logo{
  display: block;
  width: 32px;
  margin: 22px 0 0 65px;
  background: #eee;
  border-radius: 50%;
  &:hover{ background: #98FB98; }
}

.title{
  color: #fff;
  font-size: 28px;
  font-family: 'Jura', sans-serif;
  padding: 8px 22px 0 0;
  & span{
    color: #00FF7F;
    font-size: 16px;
    font-weight: 600;
  }
}
