.wrap{
  margin-top: 40px;
// margin-left: 26px;
}

.title{
  font-size: 16px;
  font-family: 'Exo 2', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #407;
  &::after{
    content: '';
    display: block;
    height: 1px;
    background: green;
    margin-top: 4px;
    margin-bottom: 8px;
  }
}

a.link {
  font-size: 20px;
  font-family: 'Exo 2', sans-serif;
  text-decoration: none;
  line-height: 1.4;
  color: green;
  &:hover{
    color: red;
  }
}
