.wrap{
  height: 48px;
  display: flex;
  justify-content:space-around;
  align-items: center;
  background: rgba(0,0,0, 0.6);
  border-radius: 0px 0px 15px 15px;
  box-shadow: 0px -3px 22px -4px #fff;
  border-top: 1px solid yellow;
}

.text{
  font-family: 'Jura', sans-serif;
  font-size: 18px;
  color: #fff;
}

.link{
  font-family: 'Jura', sans-serif;
  font-size: 18px;
  color: #fff;
  & a{
    text-decoration: none;
    border-radius: 8px;
    padding: 0 8px 0 8px;
    box-shadow: 0 0 10px rgba(250,250,250,0.9);
    color: rgba(255,0,0 , 1);
  }
  &:hover a{
    text-decoration: none;
    box-shadow: 0 0 10px rgba(250,250,250,0.0);
 }
}


@media print{
  .wrap{display: none;}
}
