.button{
  display: inline-block;
  color: white;
  font-weight: 600;
  text-decoration: none;
  font-family: 'Jura', sans-serif;
  user-select: none;
  padding: .3em 2em;
  outline: none;
  border: 1px solid;
  border-radius: 1px;
  transition: 0.2s;
  // &:hover { background: rgba(155,155,155,.2); }
  &:hover { background: #070; }
}

.button_active{ background: #090; }
.button_none{ background: #555; }
