.list{
  padding: 0;
  font-size: 16px;
  font-family: 'Exo 2', sans-serif;
  list-style-type: none;
  & li{
    font-size: 18px;
    margin-bottom: 16px;
    a{
      text-decoration: none;
      color: green;
    }
    &:hover a{
      text-decoration: none;
      color: red;
    }
  }
}

li.skype::before {
  content: '';
  background: url('../../../../img/skype.svg') no-repeat;
  padding-left: 35px;
  padding-bottom: 3px;
}

